#block-left, #block-right {
  width: 100%;
  height: 100vh;
}

#block-left {
  background-color: lightgrey;
  padding: 0 20px;
}

.aliens-list ul {
  margin: 0;
  padding: 10px 0 0 0;
  list-style: none;
  border-top: 1px solid darkgrey
}

#search-block {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

#clean-search-bttn {
  cursor: pointer;
  color: red;
}

.aliens-list ul li {
  padding: 5px 0;
  cursor: pointer;
  display: block;

}

.aliens-list ul a {
  text-decoration: none !important;
  color: black;
}

.aliens-list ul li.active {
  background-color: darkslateblue;
  color: white;
}

/* #block-right {
  background-color: lightgrey;
} */

#controls {
  width: 100%;
  height: 130px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding: 30px 0;
}

#dialog-title {
  padding: 0 20px 0 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

#dialog-footer {
  padding: 20px 0;
  display: flex;
  justify-content: flex-end;
}

.visible {
  opacity: 1;
}

.hidden {
  opacity: 0;
  pointer-events: none;
}



.App {
  text-align: center;
}
